import { styleVariables } from "projectSettings"
import { css } from "styled-components"

const element = css`
  .element-large {
    min-width: 48px;
    min-height: 48px;
    font-size: 17px;
    line-height: 24px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      & div {
        width: 36px;
        height: 36px;
      }
    }
  }
  .element-medium {
    min-width: 40px;
    min-height: 40px;
    font-size: 15px;
    line-height: 24px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      & div {
        width: 36px;
        height: 36px;
      }
    }
  }
  .element-small {
    min-width: 32px;
    min-height: 32px;
    font-size: 12px;
    line-height: 16px;
    & i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      & div {
        width: 32px;
        height: 32px;
      }
    }
  }
  button.element-large {
    grid-gap: 12px;
    padding: 12px 16px;
    font-size: 17px;
    line-height: 24px;
  }
  button.element-medium {
    grid-gap: 12px;
    padding: 8px 16px;
    font-size: 15px;
    line-height: 24px;
  }
  button.element-small {
    grid-gap: 8px;
    padding: 8px 12px;
    font-size: 12px;
    line-height: 16px;
  }
  .element-button {
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: none;
    padding: 0;
    font-size: 1rem;
    border-radius: ${styleVariables.root.primaryBorderRadius};
    &.blue {
      color: ${({ theme }) => theme.white};
      background-color: ${({ theme }) => theme.tint_blue};
    }
    &.green {
      color: ${({ theme }) => theme.white};
      background-color: ${({ theme }) => theme.tint_green};
    }
    :focus {
      outline: none;
    }
  }
`
export default element
