import { styleVariables } from "projectSettings"
import { css } from "styled-components"

const base = css`
  html,
  #___gatsby,
  #gatsby-focus-wrapper {
    width: 100%;
    height: 100%;
    overflow-y: visible;
  }
  #___gatsby {
    display: flex;
    width: 100%;
    justify-content: center;
  }
  #gatsby-focus-wrapper {
    display: flex;
    width: 100%;
    max-width: 1920px;
  }
  body {
    width: 100%;
    font-family: ${styleVariables.root.defaultFontFamily};
    background-color: ${({ theme }) => theme.background_primary};
    color: ${({ theme }) => theme.text_secondary};
    -webkit-font-smoothing: antialiased;
    @media print {
      color: black !important;
    }
  }
  ::selection {
    color: ${({ theme }) => theme.white};
    background-color: ${({ theme }) => theme.gray};
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  span {
    @media print {
      color: black !important;
    }
  }
`
export default base
