import { css } from "styled-components"

const boxShadow = css`
  .theme-shadow-border-inset-right {
    box-shadow: inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  .theme-shadow-border-inset-top {
    box-shadow: inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  .theme-shadow-border-inset-y {
    box-shadow: inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
      inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  .theme-shadow-border-inset {
    box-shadow: inset 1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
      inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
      inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
      inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  .theme-shadow-border-outset {
    box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
      -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
      0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
      0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
`

export default boxShadow
