import { styleVariables } from "projectSettings"
import { css } from "styled-components"

const border = css`
  .theme-border-radius-primary {
    border-radius: ${styleVariables.root.primaryBorderRadius};
  }
  .theme-border-radius-secondary {
    border-radius: ${styleVariables.root.secondaryBorderRadius};
  }
  .theme-border-radius-tertiary {
    border-radius: ${styleVariables.root.tertiaryBorderRadius};
  }
`

export default border
