import { css } from "styled-components"

const colorsBackground = css`
  .theme-background-primary {
    background-color: ${({ theme }) => theme.background_primary} !important;
  }
  .theme-background-secondary {
    background-color: ${({ theme }) => theme.background_secondary} !important;
  }
  .theme-background-tertiary {
    background-color: ${({ theme }) => theme.background_tertiary} !important;
  }
  .theme-background-quarternary {
    background-color: ${({ theme }) => theme.background_quarternary} !important;
  }
`
export default colorsBackground
