import { css } from "styled-components"

const typography = css`
  .large-high-title {
    font-size: 34px;
    line-height: 41px;
    font-weight: bold;
  }
  h1,
  .large-h1 {
    font-size: 28px;
    line-height: 34px;
    font-weight: normal;
  }
  h2,
  .large-h2 {
    font-size: 22px;
    line-height: 28px;
    font-weight: normal;
  }
  h3,
  .large-h3 {
    font-size: 20px;
    line-height: 24px;
    font-weight: normal;
  }
  body,
  .large-body {
    font-size: 17px;
    line-height: 22px;
    font-weight: normal;
  }
  .large-headline {
    font-size: 17px;
    line-height: 22px;
    font-weight: bold;
  }
  h4,
  .large-callout {
    font-size: 16px;
    line-height: 21px;
    font-weight: normal;
  }
  h5,
  .large-subhead {
    font-size: 15px;
    line-height: 20px;
    font-weight: normal;
  }
  h6,
  .large-caption {
    font-size: 12px;
    line-height: 16px;
    font-weight: normal;
  }
  //
  .xxxLarge-high-title {
    font-size: 40px;
    line-height: 48px;
    font-weight: normal;
  }
  .xxxLarge-h1 {
    font-size: 34px;
    line-height: 41px;
    font-weight: normal;
  }
  .xxxLarge-h3 {
    font-size: 26px;
    line-height: 32px;
    font-weight: normal;
  }
  .xxxLarge-body {
    font-size: 23px;
    line-height: 29px;
    font-weight: normal;
  }
  .xxxLarge-callout {
    font-size: 22px;
    line-height: 28px;
    font-weight: normal;
  }
  .xxxLarge-caption {
    font-size: 18px;
    line-height: 23px;
    font-weight: normal;
  }
  .xxxLarge-footnote {
    font-size: 19px;
    line-height: 24px;
    font-weight: normal;
  }
`
export default typography
