import { css } from "styled-components"

const tag_article = css`
  article {
    line-height: 24px;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      color: ${({ theme }) => theme.text_primary};
      margin-bottom: 24px;
    }
    p {
      color: ${({ theme }) => theme.text_secondary};
      margin-bottom: 24px;
    }
    img {
      border-radius: 4px;
      box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
        0px -1px 0px ${({ theme }) => theme.separator_non_opaque} !important;
    }
    video {
      border-radius: 4px;
      box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
        0px -1px 0px ${({ theme }) => theme.separator_non_opaque} !important;
      margin-bottom: 24px;
    }
    @media (max-width: 575.98px) {
      padding: 0 16px;
    }
  }
`
export default tag_article
