import { css } from "styled-components"

const colorsText = css`
  .theme-text-primary {
    color: ${({ theme }) => theme.text_primary} !important;
  }
  .theme-text-secondary {
    color: ${({ theme }) => theme.text_secondary} !important;
  }
  .theme-text-tertiary {
    color: ${({ theme }) => theme.text_tertiary} !important;
  }
  .theme-text-quarternary {
    color: ${({ theme }) => theme.text_quarternary} !important;
  }
  .theme-text-tint-blue {
    color: ${({ theme }) => theme.tint_blue} !important;
  }
`
export default colorsText
