import { css } from "styled-components"

const tag_hr = css`
  hr {
    opacity: 1;
    background-color: ${({ theme }) => theme.separator_non_opaque};
    margin-top: 24px;
    margin-bottom: 24px;
    @media print {
      border-top: solid 1px ${({ theme }) => theme.separator_non_opaque};
    }
  }
`
export default tag_hr
