import { base } from "projectSettings"
import React, { useState, useEffect } from "react"
import * as S from "./styles"
import { ThemeProvider } from "styled-components"
import { GlobalStyles } from "globalStyles"
import { lightTheme, darkTheme } from "globalStyles/theme"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"
import { setToLS, getFromLS } from "globalLibs/helpers"
import { window, document } from "browser-monads"
import { useThemeDetector } from "globalLibs/helpers"

export default function Layout(props) {
  const isDarkTheme = useThemeDetector()
  const [themeObj, setThemeObj] = useState(lightTheme)
  let themeSwitchButton
  let defaultTheme = base.siteDefaultTheme
  let userTheme = defaultTheme

  if (getFromLS("theme") === undefined) {
    setToLS("theme", defaultTheme)
  }

  const changeThemeHandler = () => {
    if (themeObj === lightTheme) {
      setThemeObj(darkTheme)
      setToLS("theme", "dark")
    }
    if (themeObj === darkTheme) {
      setThemeObj(lightTheme)
      setToLS("theme", "light")
    }
  }

  useEffect(() => {
    if (!document.getElementById("theme-toggler")) return false

    themeSwitchButton = document.getElementById("theme-toggler")
    themeSwitchButton.addEventListener("click", changeThemeHandler)

    if (getFromLS("theme") !== undefined) {
      userTheme = getFromLS("theme").replace(/"/g, "")
      userTheme === "light" ? setThemeObj(lightTheme) : setThemeObj(darkTheme)
    }
    return () => {
      themeSwitchButton.removeEventListener("click", changeThemeHandler)
    }
  }, [themeObj])

  return (
    <ThemeProvider theme={themeObj}>
      <GlobalStyles />
      <S.Layout as="main" className={props.className} id={props.id}>
        <Sidebar />
        <MainNav></MainNav>
        <div className="main-content mt-48 mb-56">{props.children}</div>
      </S.Layout>
    </ThemeProvider>
  )
}

