import { createGlobalStyle } from "styled-components"
import adaptive from "./modules/adaptive"
import base from "./modules/base"
import border from "./modules/border"
import boxShadow from "./modules/box-shadow"
import colorsBackground from "./modules/colors-background"
import colorsText from "./modules/colors-text"
import element from "./modules/element"
import fixes from "./modules/fixes"
import scrollbar from "./modules/scrollbar"
import tag_a from "./modules/tag_a"
import tag_article from "./modules/tag_article"
import tag_figcaption from "./modules/tag_figcaption"
import tag_hr from "./modules/tag_hr"
import tag_ul from "./modules/tag_ol,ul"
import typography from "./modules/typography"

export const GlobalStyles = createGlobalStyle`
  ${adaptive}
  ${base}
  ${border}
  ${boxShadow}
  ${colorsBackground}
  ${colorsText}
  ${element}
  ${scrollbar}
  ${tag_a}
  ${tag_article}
  ${tag_hr}
  ${tag_ul}
  ${tag_figcaption}
  ${typography}
  ${fixes} // global overwrites
`
